#skills {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: #192028;
    .container {
        width: 100%;
        max-width: 1480px;
        margin: 0 auto;
        .title {
            width: 100%;
            padding: 40px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            h2 {
                font-size: 90px;
                font-weight: 800;
                line-height: 120%;
                color: #fff;
                padding: 20px 0;
                &:hover {
                    span {
                        animation: bounce 0.6s;
                    }
                }
                i {
                    font-size: 48px;
                    color: #fff;
                    margin-right: 30px;
                    border-bottom: 0px;
                }
                span {
                    display: inline-block;
                    color: #fff;
                    text-shadow: 0 1px 0 #222, 0 2px 0 #333, 0 3px 0 #444, 0 4px 0 #555,
                    0 5px 0 #666, 0 6px 0 #777, 0 7px 0 #888, 0 8px 0 #999, 0 9px 0 #fff,
                    0 50px 25px rgba(0, 0, 0, 0.2);
                    animation: float 0.2s ease-in-out infinite;
                    @keyframes float {
                        0% {
                            transform: none;
                        }
                        100% {
                            transform: none;
                        }
                        33% {
                            transform: translateY(-0.3px) rotate(-2deg);
                        }
                        66% {
                            transform: translateY(0.3px) rotate(2deg);
                        }
                    }
                    &:nth-child(2) {
                        animation-delay: 0.05s;
                    }
                    &:nth-child(3) {
                        animation-delay: 0.1s;
                    }
                    &:nth-child(4) {
                        animation-delay: 0.15s;
                    }
                    &:nth-child(5) {
                        animation-delay: 0.2s;
                    }
                    &:nth-child(6) {
                        animation-delay: 0.25s;
                    }
                    &:nth-child(7) {
                        animation-delay: 0.3s;
                    }
                    &:nth-child(8) {
                        animation-delay: 0.35s;
                    }
                    &:nth-child(9) {
                        animation-delay: 0.4s;
                    }
                }
            }
            &.ani {
                animation: AboutMeInfo 2s ease-in-out forwards;                           
            }               
            @keyframes AboutMeInfo {
                0%   {opacity: 0; transform: translateY(200px);}
                100% {opacity: 1; transform: translateY(0);}
            }
        }
        .content {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center; 
            .skills_LB {
                width: 570px;
                margin-right: 50px;
                padding: 50px 10px;
                background: #ffc400;
                border: 1px solid #ffc400;
                border-radius: 80px;
                display: flex;
                justify-content: center;
                align-items: center;
                .lb_box {
                    width: 80%;
                    padding: 20px;
                    background: #fff;
                    border-radius: 50px;
                    display: flex;
                    flex-flow: wrap;
                    justify-content: center;
                    align-items: center;
                    .box_title {
                        width: 100%;
                        padding: 0 0 20px 0;
                        display: block;
                        text-align: center;
                        strong {
                            position: relative;
                            font-size: 37px;
                            font-weight: 600;
                            line-height: 40px;
                            padding-bottom: 5px;
                            color: #f4623a;
                            &:hover {
                                &::after {
                                    width: 100%;
                                }
                            }
                            &::after {
                                content: "";
                                position: absolute;
                                top: 99%;
                                left: 50%; 
                                transform: translateX(-50%);    
                                width: 0;  
                                height: 3px;
                                background: #f4623a;
                                transition: all 0.3s;
                            }
                        }
                    }
                    li {
                        display: inline-block;
                        width: 130px;
                        padding: 10px 20px;
                        span {
                            width: 80px;
                            height: 80px;
                            overflow: hidden;
                            text-align: center;
                            img {
                                width:  90px;
                                height: 100px;
                                transition: all 0.2s ease-in-out;
                                &:hover {
                                    scale: 1.1;
                                }
                            }
                        }
                    }
                }
                &.ani {
                    animation: skillsLB 3s ease-in-out forwards;                           
                }               
                @keyframes skillsLB {
                    0%   {opacity: 0; transform: translateX(-400px);}
                    100% {opacity: 1; transform: translateX(0);}
                }
            }
            .skills_RB {
                width: 700px;
                display: flex;
                flex-flow: wrap;
                justify-content: center;
                align-items: center;
                .gap {
                    width: 300px;
                    padding: 40px 0;
                    background: #ffc400;
                    border: 1px solid #ffc400;
                    border-radius: 80px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &.odd {
                        width: 350px;
                        margin-right: 50px;
                    }
                    &:nth-child(1) {
                        margin-bottom: 20px;
                    }
                    .lb_box {
                        width: 80%;
                        padding: 20px;
                        background: #fff;
                        border-radius: 50px;
                        display: flex;
                        flex-flow: wrap;
                        justify-content: center;
                        align-items: center;
                        .box_title {
                            width: 100%;
                            padding: 0 0 20px 0;
                            display: block;
                            text-align: center;
                            strong {
                                position: relative;
                                width: 100%;
                                font-size: 24px;
                                font-weight: 600;
                                line-height: 40px;
                                padding-bottom: 5px;
                                color: #f4623a;
                                &:hover {
                                    &::after {
                                        width: 100%;
                                    }
                                }
                                &::after {
                                    content: "";
                                    position: absolute;
                                    top: 99%;
                                    left: 50%; 
                                    transform: translateX(-50%);    
                                    width: 0;  
                                    height: 3px;
                                    background: #f4623a;
                                    transition: all 0.3s;
                                }
                                &.bstext {
                                    font-size: 27px;
                                }
                                &.bstext1 {
                                    font-size: 21px;
                                }
                            }
                        }
                        li {
                            display: inline-block;
                            width: 70px;
                            padding: 5px 10px;
                            text-align: center;
                            &.back {
                                width: 50%;
                            }
                            &.even {
                                width: 50%;
                            }
                            span {
                                width: 40px;
                                height: 40px;
                                overflow: hidden;
                                text-align: center;
                                img {
                                    width:  60px;
                                    height: 60px;
                                    transition: all 0.2s ease-in-out;
                                    &:hover {
                                        scale: 1.2;
                                    }
                                }
                            }
                        }
                    }
                }
                &.ani {
                    animation: skillsRB 3.3s ease-in-out forwards;                           
                }               
                @keyframes skillsRB {
                    0%   {opacity: 0; transform: translateX(800px);}
                    100% {opacity: 1; transform: translateX(0);}
                }
            }
        }
    }
}